import React, { useEffect, useState } from "react";
import {
  useAppDispatch,
  useAppDispatchEffect,
  useAppSelector,
} from "../../../App.reducer";
import { RouteComponentProps } from "react-router";
import {
  clearWorkFlowCacheAction,
  resetLoadingStatesAction,
} from "../Works/actions";
import { getWorkValidationSchemaSelector } from "../../../validationSchema/selector";
import { AccountRoles, LoadingState } from "../../../types/enums";
import {
  getWorkCloneLoadingStateSelector,
  getWorkCloneSelector,
  getWorkRegistrationsSelector,
  getWorkSelector,
  getWorksListLinks,
  getWorksListLoadingStateSelector,
} from "../ListWorks/selectors";
import {
  getAccountSelector,
  getCompanySelector,
  getUserIsSocAdminSelector,
} from "../SignIn/selectors";
import {
  getIpiListLoadingStateSelector,
  getIpiListSelector,
} from "../Ipi/selectors";
import { getSidebarOffsetSelector } from "../Sidebar/selectors";
import {
  fetchWorkEffect,
  fetchWorkRegistrationsEffect,
  fetchWorksEffect,
} from "../Works/effects";
import { addAlertAction } from "../Alerts/actions";
import { AlertType } from "../Alerts/actions.types";
import { push } from "connected-react-router";
import { getParentPath } from "./utils";
import { doesUserHaveRoles } from "../../../utils/helpers";
import { fetchWorkValidationSchemaEffect } from "../../../validationSchema/effects";
import { PageWorkRegistrationLoading } from "../../molecules/PageWorkRegistrationLoading";
import { FormWorkRegistration } from "../../organisms/FormWorkRegistration";
import { WorkResponse } from "../../../api.types";
import { paths } from "../../../utils/common";

export const WorkRegistration: React.FC<RouteComponentProps<{ id: string }>> =
  ({
    match: {
      params: { id },
    },
    location: { pathname },
    history,
  }) => {
    const dispatchEffect = useAppDispatchEffect();
    const dispatch = useAppDispatch();
    const state = useAppSelector((state) => state);
    const work = getWorkSelector(state);
    const workClone = getWorkCloneSelector(state);
    const worksListLoadingState = getWorksListLoadingStateSelector(state);
    const workCloneLoadingState = getWorkCloneLoadingStateSelector(state);
    const worksListLinks = getWorksListLinks(state);
    const workSchema = getWorkValidationSchemaSelector(state);
    const account = getAccountSelector(state);
    const company = getCompanySelector(state);
    const ipiList = getIpiListSelector(state);
    const ipiListLoadingState = getIpiListLoadingStateSelector(state);
    const workRegistrations = getWorkRegistrationsSelector(state);
    const breakpointOffset = getSidebarOffsetSelector(state);
    const isUserSocAdmin = getUserIsSocAdminSelector(state);
    const isPublisher = doesUserHaveRoles(state, [AccountRoles.PUBLISHER]);
    const isCreator = doesUserHaveRoles(state, [AccountRoles.CREATOR]);

    const [isFormDataLoading, setIsFormDataLoading] = useState(Boolean(id));

    const fetchWork = async (id: string) => {
      try {
        const { response }: WorkResponse = await dispatchEffect(
          fetchWorkEffect(id)
        );
        if (response.work.item._links.listWorkRegistrationRequests) {
          await dispatchEffect(fetchWorkRegistrationsEffect(id));
        }
      } catch (e) {
        dispatch(addAlertAction(AlertType.ERROR, e.message));
        dispatch(push(getParentPath()));
      } finally {
        setIsFormDataLoading(false);
      }
    };

    useEffect(() => {
      if (id && work?.guid !== id) {
        dispatch(resetLoadingStatesAction());
        fetchWork(id);
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id, dispatch, isUserSocAdmin]);

    useEffect(() => {
      dispatchEffect(fetchWorkValidationSchemaEffect());
    }, [dispatchEffect]);

    useEffect(() => {
      const fetchWorks = async () => {
        if (
          worksListLoadingState === LoadingState.PRISTINE &&
          !worksListLinks
        ) {
          try {
            await dispatchEffect(fetchWorksEffect({ isDraftsPage: true }));
          } catch (e) {
            if (pathname === paths.WORKS_REGISTER) {
              history.push(paths.WORKS_LIST);
            }
          }
        }
      };

      fetchWorks();
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(
      () => () => {
        dispatch(clearWorkFlowCacheAction());
      },
      [dispatch]
    );

    const breadCrumb = id ? "Edit" : "New";

    return (
      <React.Fragment>
        {isFormDataLoading ||
        [ipiListLoadingState, workCloneLoadingState].includes(
          LoadingState.FETCHING
        ) ||
        !worksListLinks ? (
          <PageWorkRegistrationLoading
            dispatch={dispatch}
            breadCrumb={breadCrumb}
          />
        ) : (
          <FormWorkRegistration
            dispatch={dispatch}
            dispatchEffect={dispatchEffect}
            breadCrumb={breadCrumb}
            history={history}
            account={account}
            work={id ? work : workClone}
            workListItemsLinks={worksListLinks}
            workValidationSchema={workSchema}
            workRegistrations={workRegistrations}
            ipiList={ipiList}
            breakpointOffset={breakpointOffset}
            isPublisher={isPublisher}
            isCreator={isCreator}
            isSocAdmin={isUserSocAdmin}
            company={company}
          />
        )}
      </React.Fragment>
    );
  };
