import {
  _WorkListItemLinks,
  _WorkRegistrationsLinks,
  ListMeta,
  REGISTERED,
  Work,
  WorkListItem,
  WorkRegistration,
} from "../../../utils/models";
import * as types from "./actions.types";
import { LoadingState, PartyRoleType } from "../../../types/enums";
import { initialWorksDatabase } from "../../../mocks";
import { ipiDatabase } from "../../../__mocks__/constants";
import { REGISTER_WORK_SUCCESS } from "../WorkRegistration/actions.types";

export interface WorksState {
  workListMeta?: ListMeta;
  workListItems: WorkListItem[];
  workListItemsType?: "DRAFTS" | "WORKS";
  work?: Work;
  workIndex?: number;
  worksDatabase: Work[];
  workClone?: Work;
  workRegistrations: WorkRegistration[];
  fetchWorkLoadingState: LoadingState;
  workCloneLoadingState: LoadingState;
  fetchWorkRegistrationsLoadingState: LoadingState;
  updateWorkLoadingState: LoadingState;
  fetchWorksLoadingState: LoadingState;
  _workListItemsLinks?: _WorkListItemLinks;
  _workRegistrationsLinks?: _WorkRegistrationsLinks;
}

export const worksInitialState: WorksState = {
  workListItems: [],
  workRegistrations: [],
  worksDatabase: initialWorksDatabase,
  fetchWorkLoadingState: LoadingState.PRISTINE,
  workCloneLoadingState: LoadingState.PRISTINE,
  updateWorkLoadingState: LoadingState.PRISTINE,
  fetchWorksLoadingState: LoadingState.PRISTINE,
  fetchWorkRegistrationsLoadingState: LoadingState.PRISTINE,
};

export const worksReducer = (
  state: WorksState = worksInitialState,
  action: types.WorksActionTypes
): WorksState => {
  switch (action.type) {
    case types.CLEAR_WORK_FLOW_CACHE: {
      return {
        ...state,
        workRegistrations: [],
        work: undefined,
        workIndex: undefined,
        workClone: undefined,
        _workListItemsLinks: undefined,
        _workRegistrationsLinks: undefined,
      };
    }

    case types.POST_WORK_SUCCESS: {
      return {
        ...state,
        workIndex: state.worksDatabase.length,
        worksDatabase: [...state.worksDatabase, action.work],
      };
    }

    case types.FETCH_WORKS: {
      return {
        ...state,
        workListItemsType: action.worksType,
        fetchWorksLoadingState: action.next
          ? LoadingState.FETCHED
          : LoadingState.FETCHING,
      };
    }

    case types.FETCH_WORKS_SUCCESS: {
      const allWorkItems = action.next
        ? [...state.workListItems, ...action.works]
        : [...action.works];
      const filteredWorkItems = allWorkItems.filter((i) =>
        action.worksType === "DRAFTS"
          ? i.status === "DRAFT"
          : i.status !== "DRAFT"
      );
      return {
        ...state,
        workListItems: filteredWorkItems,
        workListMeta: action.meta,
        _workListItemsLinks: action._links,
        workListItemsType: action.worksType,
        fetchWorksLoadingState: LoadingState.FETCHED,
      };
    }

    case types.FETCH_WORKS_FAILURE: {
      return {
        ...state,
        fetchWorksLoadingState: LoadingState.ERROR,
      };
    }

    case types.FETCH_WORK: {
      return {
        ...state,
        workIndex: undefined,
        fetchWorkLoadingState: LoadingState.FETCHING,
      };
    }

    case types.FETCH_WORK_SUCCESS: {
      return {
        ...state,
        fetchWorkLoadingState: LoadingState.FETCHED,
        workIndex: action.itemIndex,
      };
    }

    case types.PATCH_WORK_SUCCESS: {
      const workId = action.id;
      const workToPatch = state.worksDatabase.find(
        (work) => work.guid === workId
      );
      if (!workToPatch) return state;
      if (action.data.altTitle) {
        workToPatch.altTitle = action.data.altTitle;
      }
      if (action.data.titleLanguageCode) {
        workToPatch.titleLanguageCode = action.data.titleLanguageCode;
      }
      if (action.data.firstReleaseDate) {
        workToPatch.firstReleaseDate = action.data.firstReleaseDate;
      }
      if (action.data.duration) {
        workToPatch.duration = action.data.duration;
      }
      if (action.data.hasBeenRecorded) {
        workToPatch.hasBeenRecorded = action.data.hasBeenRecorded;
      }
      if (action.data.title) {
        workToPatch.title = action.data.title;
      }
      if (action.data.musicalDistributionCategory) {
        workToPatch.musicalDistributionCategory =
          action.data.musicalDistributionCategory;
      }
      console.log(action);
      if (action.data.creators) {
        action.data.creators.forEach((creator, index) => {
          if (creator.op === "ADD") {
            const ipiNameNumber = creator.creator.ipiNameNumber;
            const ipiToAdd = ipiDatabase.find(
              (i) => i.ipiNameNumber === ipiNameNumber
            );
            if (!ipiToAdd) return;
            workToPatch.creators.push({
              created: {
                iso: new Date().toISOString(),
                timestamp: new Date().getTime(),
              },
              ipi: {
                ipiNameNumber: creator.creator.ipiNameNumber,
                ipiReference: ipiToAdd.ipiReference,
                verified: ipiToAdd.verified,
              },
              isSelf: false,
              profile: ipiToAdd.profile,
              role: creator.creator.role || PartyRoleType.C,
              updated: {
                iso: new Date().toISOString(),
                timestamp: new Date().getTime(),
              },
              mrShare: creator.creator.mrShare,
              prShare: creator.creator.prShare,
            });
          }
          if (creator.op === "PATCH") {
            workToPatch.creators.forEach((c) => {
              if (c.ipi.ipiNameNumber === creator.creator.ipiNameNumber) {
                if (creator.creator.mrShare) {
                  c.mrShare = creator.creator.mrShare;
                }
                if (creator.creator.prShare) {
                  c.prShare = creator.creator.prShare;
                }
                if (creator.creator.role) {
                  c.role = creator.creator.role;
                }
              }
            });
          }
          (creator?.creator?.publishers || []).forEach((publisher) => {
            if (publisher.op === "ADD") {
              workToPatch.creators[
                workToPatch.creators.length - 1
              ].publishers?.push({
                creatorPublisherSocietyAgreementNumber:
                  publisher.publisher.creatorPublisherSocietyAgreementNumber,
                publisherSocietyAgreementNumber:
                  publisher.publisher.creatorPublisherSocietyAgreementNumber,
                ipiNameNumber: publisher.publisher.ipiNameNumber,
                mrShare: publisher.publisher.mrShare,
                prShare: publisher.publisher.prShare,
              });
            }
          });
        });
      }

      return {
        ...state,
        worksDatabase: state.worksDatabase.map((work) => {
          if (work.guid === workId) {
            return workToPatch;
          }
          return work;
        }),
      };
    }

    case types.UPDATE_WORK: {
      return { ...state, updateWorkLoadingState: LoadingState.FETCHING };
    }

    case types.UPDATE_WORK_SUCCESS: {
      return { ...state, updateWorkLoadingState: LoadingState.FETCHED };
    }

    case REGISTER_WORK_SUCCESS: {
      return {
        ...state,
        worksDatabase: state.worksDatabase.map((w, i) => {
          if (i === state.workIndex) {
            return {
              ...w,
              status: "IN_PROGRESS" as any,
            };
          }
          return w;
        }),
      };
    }

    case types.UPDATE_WORK_FAILURE: {
      return { ...state, updateWorkLoadingState: LoadingState.ERROR };
    }

    case types.RESET_LOADING_STATES: {
      return {
        ...state,
        fetchWorkLoadingState: LoadingState.PRISTINE,
        updateWorkLoadingState: LoadingState.PRISTINE,
      };
    }

    case types.LEAVE_WORK_SUCCESS: {
      return {
        ...state,
        workListItems: state.workListItems.filter(
          (el) => el.guid !== action.workGuid
        ),
      };
    }

    case types.FETCH_WORK_REGISTRATIONS: {
      return {
        ...state,
        fetchWorkRegistrationsLoadingState: LoadingState.FETCHING,
      };
    }

    case types.FETCH_WORK_REGISTRATIONS_SUCCESS: {
      return {
        ...state,
        workRegistrations: action.workRegistrations,
        fetchWorkRegistrationsLoadingState: LoadingState.FETCHED,
        _workRegistrationsLinks: action._links,
      };
    }

    case types.FETCH_WORK_REGISTRATIONS_FAILURE: {
      return {
        ...state,
        fetchWorkRegistrationsLoadingState: LoadingState.ERROR,
      };
    }

    case types.WORK_CLONE: {
      return {
        ...state,
        workCloneLoadingState: LoadingState.FETCHING,
        workClone: undefined,
      };
    }

    case types.WORK_CLONE_SUCCESS: {
      return {
        ...state,
        workCloneLoadingState: LoadingState.FETCHED,
        workClone: action.work,
      };
    }

    case types.WORK_CLONE_FAILURE: {
      return {
        ...state,
        workCloneLoadingState: LoadingState.ERROR,
        workClone: undefined,
      };
    }

    case types.CHANGE_CREATOR_IPI_SUCCESS: {
      return {
        ...state,
        work: action.work,
      };
    }

    default:
      return state;
  }
};
